.standard-title-description {
  padding-top: 16px;
  padding-bottom: 16px;
}

.standard-title-description__title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: @second-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 26px;
  }
}

.standard-title-description__description {
  margin: 0;
  margin-top: 24px;
  text-align: center;
  text-transform: uppercase;
}
